<template>
  <div class="puzzle">
    <transition-group name="list">
      <div
        v-for="(cell, index) in slots"
        class="puzzle__cell"
        :class="'puzzle__cell--' + cell.id"
        :key="cell.id"
        @click="swapCell(index)"
      >
        <p>{{ cell.text }}</p>
      </div>
    </transition-group>
  </div>
</template>

<script>
export default {
  name: "PuzzleComponent",
  data() {
    return {
      slots: [
        {
          id: 1,
          text: "1",
        },
        {
          id: 2,
          text: "2",
        },
        {
          id: 3,
          text: "3",
        },
        {
          id: 4,
          text: "4",
        },
        {
          id: 5,
          text: "5",
        },
        {
          id: 6,
          text: "6",
        },
        {
          id: 7,
          text: "7",
        },
        {
          id: 8,
          text: "8",
        },
        {
          id: 9,
          text: "",
        },
        {
          id: 10,
          text: "9",
        },
      ],
      switchable_map: {
        0: [1, 3],
        1: [0, 2, 4],
        2: [1, 5],
        3: [0, 4, 6],
        4: [1, 3, 5, 7],
        5: [2, 4, 8],
        6: [3, 7],
        7: [4, 6, 8],
        8: [5, 7, 9],
        9: [8],
      },
      blankIndex: 8,
    };
  },
  methods: {
    swapCell(index) {
      if (index === this.blankIndex) {
        return 0;
      }
      if (this.switchable_map[this.blankIndex].includes(index)) {
        let temp = this.slots[this.blankIndex];
        this.slots[this.blankIndex] = this.slots[index];
        this.slots[index] = temp;
        this.blankIndex = index;
      }
    },
  },
};
</script>

<style scoped>
.list-move, /* apply transition to moving elements */
.list-enter-active,
.list-leave-active {
  transition: all 0.5s ease;
}

.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateX(30px);
}

/* ensure leaving items are taken out of layout flow so that moving
   animations can be calculated correctly. */
.list-leave-active {
  position: absolute;
}
</style>
